export default {
    data(){
        return {
            guide:{}
        }
    },
    created(){
        this.getGuide();
    },
    methods:{
        getGuide(){
            this.$http.get('/guide/get_guide/?cgid='+this.$route.query.id).then(res=>{
                if(res.status == 200){
                    this.guide = res.data.guide;
                }else{
                    this.guide = {};
                }
            })
        }
    }
}



