import SelectAddress from "@/pages/common/selectAddress";
import NavHeader from '@/components/navHeader/index';
import DateTimePicker from '@/components/dateTimePicker/index';
import { getNowFormatDate, changeTitle } from '@/common/common';
export default {
    components: { NavHeader, DateTimePicker, SelectAddress },
    data(){
        return{
            type: this.$route.query.type,
            showDateTimePicker: false,
            moveHouseData:{
                pricedetail:{}
            },
            time:getNowFormatDate(),
            formData:{
                "smsid": this.$route.query.id,
                "umtstarttime": '',
                "umtmoveoutaddr": "",
                "umtmoveoutlocation": "",
                "umtmoveinaddr": "",
                "umtmoveinlocation": "",
                "umtphone": "",
                "umtspecialwish": "",
                "umtpreviewprice":'',
            },
            // 选址
            selectAddressType: "",
            showSelectAddress: false,
            distance: '',
        }
    },
    created(){
        this.getDetail();
    },
    mounted(){
        changeTitle('搬家预约');
    },
    methods:{
        totalPrice(){
            let overDistance = this.distance - this.moveHouseData. pricedetail .smsppricestartdeadline<0?0:this.distance - this.moveHouseData. pricedetail .smsppricestartdeadline;
            return this.moveHouseData. pricedetail .smspstartprice + overDistance*this.moveHouseData. pricedetail .smsppricesoverstartperkg
        },
        selectLocation(type){
            this.selectAddressType = type;
            this.showSelectAddress = true;
        },
        getAddressJson(addressJson){
            if(addressJson.type=="out"){
                this.formData.umtmoveoutaddr = addressJson.addressDetail+ addressJson.number+ addressJson.floor;
                this.formData.umtmoveoutlocation = addressJson.location.lng+','+addressJson.location.lat;
            }else{
                this.formData.umtmoveinaddr = addressJson.addressDetail+ addressJson.number+ addressJson.floor;
                this.formData.umtmoveinlocation = addressJson.location.lng+','+addressJson.location.lat;
            }
            if(this.formData.umtmoveoutlocation&&this.formData.umtmoveinlocation){
                this.getDistance(this.formData.umtmoveoutlocation, this.formData.umtmoveinlocation);
            }
        },
        selectTime(){
            this.showDateTimePicker = true;
        },
        getTime(time){
            this.time = time;
            this.formData.umtstarttime = time.replace(/-/g,'').replace(' ','').replace(/:/g,'')
        },
        // 获取搬家服务详情
        getDetail(){
            this.$http.get('/mover/get_detail/',{params:{smsid:this.$route.query.id}}).then(res=>{
                if(res.status == 200){
                    this.moveHouseData = res.data.detail;
                }
            })
        },
        getDistance(from, to){
            var map = new BMap.Map('')
            var pointA = new BMap.Point(parseFloat(to.split(',')[0]), parseFloat(to.split(',')[1]))  // 店铺的经纬度
            var pointB = new BMap.Point(parseFloat(from.split(',')[0]), parseFloat(from.split(',')[1]))  // 店铺的经纬度
            this.distance = (map.getDistance(pointA, pointB) / 1000).toFixed(2) // 保留小数点后两位
        },
        // 提交数据
        submitData(){
            this.formData.umtpreviewprice = this.totalPrice();
            this.formData.umtstarttime = this.time.replace(/-/g,'').replace(/:/,'').replace(" ",'');
            if(!this.formData.umtmoveoutaddr){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入搬出地",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.umtmoveinaddr){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入搬入地",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.umtphone){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入手机号",
                    position:'middle'
                });
                return false;
            }
            // console.log(this.formData)
            this.$http({
                url: '/trade/mover_appointment/',
                method: 'post',
                data: this.formData,
                params:{token: localStorage.getItem('token')}
            }).then(res=>{
                if(res.status == 200){
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                    this.$router.push({path:'/myOrder', query:{from:'moveHouse'}})
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
            })
        }
    }
}



