
import jsonp from 'jsonp'
import NoData from '@/components/noData/index';
import NavHeader from '@/components/navHeader/index';
import Icon from 'vux/src/components/icon/index.vue'

import { changeTitle, wxConfig } from '@/common/common';
export default {
components: { NavHeader, NoData, Icon },
data(){
return {
isOpen: false,
cityRepair: localStorage.getItem('cityRepair')?localStorage.getItem('cityRepair'):'定位中',
cityCodeRepair:'',
showSelectCity: false,
cityList: []
}
},
mounted(){
changeTitle('邻家维修');
wxConfig(['getLocation']);
if(!localStorage.getItem('cityRepair')){
this.getLocation();
}else{
this.getData();
}
},
created(){
this.getData();
},
methods:{
getLocation(){
var _this = this;
this.$wechat.ready(()=>{
this.$wechat.getLocation({
type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
if (err) {
console.error(err.message);
} else {
_this.cityRepair=data.result.addressComponent.city;
_this.cityCodeRepair = data.result.addressComponent.adcode.slice(0,4)+'00';
localStorage.setItem('cityRepair',_this.cityRepair);
localStorage.setItem('cityCodeRepair',_this.cityCodeRepair);
_this.getData();
}
})
},
fail: function (err) {
_this.$vux.toast.show({
type:'text',
text: "定位失败",
position:'middle'
})
}
})
})
},
checkMove(path){
if(localStorage.getItem('token')){
this.$router.push(path);
}else{
this.$router.push({path:'/login',query:{from:'repair'}})
}

},
toMyOrder(){
if(!localStorage.getItem('token')){
this.$router.push({path:'/login',query:{from:'myOrder'}})
}else{
this.$router.push({path:'/myOrder',query:{from:'repair'}})
}
},
getData(){
this.$http.get('/fixer/fixer_list/',{params:{city_id:localStorage.getItem('cityCodeRepair')}}).then(res=>{
if(res.status == 200){
this.isOpen = true;
}else{
this.isOpen = false;
}
})
},
selectCity(){
this.showSelectCity = true;
this.$http.get('/fixer/city_list/').then(res=>{
if(res.status == 200){
this.cityList = res.data.citys;
}else{
this.cityList = [];
}
})
},
changeCity(item){
this.showSelectCity = false;
localStorage.setItem('cityRepair',item.name);
this.cityRepair = item.name;
localStorage.setItem('cityCodeRepair',item.city_id);
this.getData();
}
}
}



