export default {
    data(){
        return{
            price:['',''],
        }
    },
    methods:{
        // 筛选
        activeTab(array, index){
            if(array.indexOf(index)==-1){
                array.push(index);
            }else{
                array.splice(array.indexOf(index),1);
            }
        },
        // 租金选择
        rentMoneySelect(min, max){
            this.price = [min, max];
            this.submitData();
        },
        // 传递数据
        submitData(){
            let filterData = {};
            filterData.lowprice = this.price[0]?this.price[0]:null;
            filterData.highprice = this.price[1]?this.price[1]:null;
            this.$emit('updateIsShow',false);
            this.$emit('getRentPriceFilter',filterData);
        },
        resetData(){
            this.price = ['','']
            let filterData = {
                lowprice: null,
                highprice: null,
            };
            this.$emit('updateIsShow',false);
            this.$emit('getRentPriceFilter',filterData);
        }
    }
}



