import FlatItem from '@/components/flatItem/index';
import NavHeader from '@/components/navHeader/index';
import { changeTitle } from '@/common/common';
export default {
    components: { FlatItem, NavHeader },
    data () {
        return {
            flatData:[],
            query:{//查询参数
                page: 1,
                count: 5,
                type: '2'
            },
        }
    },
    beforeRouteEnter (to, from, next) {
        // 记录滚动条位置
        if(!sessionStorage.askPositon || from.path == '/home'){//当前页面刷新不需要切换位置
            sessionStorage.askPositon = '';
            next(vm=>{
                vm.query = {//查询参数
                    page: 1,
                    count: 5,
                    type: '2'
                }
                vm.getData(vm.query, ()=>{});
            });
        }else{
            next(vm => {
                if(vm && vm.$refs.my_scroller){//通过vm实例访问this
                    setTimeout(function () {
                        vm.$refs.my_scroller.scrollTo(0, sessionStorage.askPositon, 0);
                    },5)//同步转异步操作
                }
            })
        }
    },
    beforeRouteLeave(to,from,next){//记录离开时的位置
        sessionStorage.askPositon = this.$refs.my_scroller && this.$refs.my_scroller.getPosition() && this.$refs.my_scroller.getPosition().top;
        next();
    },
    created(){
        //this.getPosition();
        this.getData(this.query, ()=>{});
    },
    mounted(){
        changeTitle('邻家公寓');
    },
    methods:{
        getData(query, done){
            this.$http.get('/room/get_list/',{params:query}).then(res=>{
                if(res.status == 200){
                    if(res.data.length>0){
                        this.flatData = res.data;
                        done();
                    }else{
                        this.flatData = [];
                        done();
                    }
                }
            })
        },
        // 刷新
        refresh(done){
            this.query.page = 1;
            this.getData(this.query,done);
        },
        // 加载数据
        infinite(done){
            this.query.page++;
            this.$http.get('/room/get_list/',{params: this.query}).then(res=>{
                if(res.data.length>0){
                    this.flatData = this.flatData.concat(res.data);
                    done()
                }else{
                    done(true)
                }
            })
        }
    }
}



