import { changeTitle } from '@/common/common'
export default {
    mounted(){
        changeTitle('业主委托');
    },
    methods:{
        toEntrustmentDetail(){
            if(localStorage.getItem('token')){
                this.$router.push('/entrustmentDetail');
            }else{
                this.$router.push('/login?from=entrustmentDetail');
            }
        }
    }
}



