import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login/login'
import Home from '@/pages/home/home'
import FriendsFamily from '@/pages/friendsFamily/friendsFamily'
import Flat from '@/pages/flat/flat'
import Homestay from '@/pages/homestay/homestay'
import HomestayIntroduce from '@/pages/homestay/homestayIntroduce'
import HouseDetail from '@/pages/common/houseDetail'
import Clean from '@/pages/clean/clean'
import CleanOrder from '@/pages/clean/cleanOrder'
import Repair from '@/pages/repair/repair'
import RepairOrder from '@/pages/repair/repairOrder'
import MoveHouse from '@/pages/moveHouse/moveHouse'
import MoveHouseOrder from '@/pages/moveHouse/moveHouseOrder'
import OwnerEntrustment from '@/pages/ownerEntrustment/entrustment'
import EntrustmentDetail from '@/pages/ownerEntrustment/entrustmentDetail'
import SelectCity from '@/pages/common/selectCity'
import Filter from '@/pages/common/filter'
import MyOrder from '@/pages/myOrder/myOrder'
import FixerOrderDetail from '@/pages/myOrder/fixerOrderDetail'
import CleanerOrderDetail from '@/pages/myOrder/cleanerOrderDetail'
import MoverOrderDetail from '@/pages/myOrder/moverOrderDetail'
import Help from '@/pages/help/help'
import HelpDetail from '@/pages/help/helpDetail'
import Complain from '@/pages/complain/complain'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			redirect: '/home'
		},
		{
			path: '/login',
			name: 'Login',
			component: Login
		},
		{
			path: '/home',
			name: 'Home',
			component: Home
		},
		{
			path: '/friendsFamily',
			name: 'FriendsFamily',
			component: FriendsFamily,
			meta:{
				keepAlive: true
			}
		},
		{
			path: '/flat',
			name: 'Flat',
			component: Flat,
			meta:{
				keepAlive: true
			}
		},
		{
			path: '/homestay',
			name: 'Homestay',
			component: Homestay,
			meta:{
				keepAlive: true
			}
		},
		{
			path: '/homestayIntroduce',
			name: 'HomestayIntroduce',
			component: HomestayIntroduce
		},
		{
			path: '/houseDetail',
			name: 'HouseDetail',
			component: HouseDetail
		},
		{
			path: '/selectCity',
			name: 'SelectCity',
			component: SelectCity
		},
		{
			path: '/filter',
			name: 'Filter',
			component: Filter
		},
		{
			path: '/clean',
			name: 'Clean',
			component: Clean
		},
		{
			path: '/cleanOrder',
			name: 'CleanOrder',
			component: CleanOrder
		},
		{
			path: '/repair',
			name: 'Repair',
			component: Repair
		},
		{
			path: '/repairOrder',
			name: 'RepairOrder',
			component: RepairOrder
		},
		{
			path: '/moveHouse',
			name: 'MoveHouse',
			component: MoveHouse
		},
		{
			path: '/moveHouseOrder',
			name: 'MoveHouseOrder',
			component: MoveHouseOrder
		},
		{
			path: '/ownerEntrustment',
			name: 'OwnerEntrustment',
			component: OwnerEntrustment
		},
		{
			path: '/entrustmentDetail',
			name: 'EntrustmentDetail',
			component: EntrustmentDetail
		},
		{
			path: '/myOrder',
			name: 'MyOrder',
			component: MyOrder,
			meta:{
				keepAlive: true
			}
		},
		{
			path: '/fixerOrderDetail',
			name: 'FixerOrderDetail',
			component: FixerOrderDetail
		},
		{
			path: '/cleanerOrderDetail',
			name: 'CleanerOrderDetail',
			component: CleanerOrderDetail
		},
		{
			path: '/moverOrderDetail',
			name: 'MoverOrderDetail',
			component: MoverOrderDetail
		},
		{
			path: '/help',
			name: 'Help',
			component: Help
		},
		{
			path: '/helpDetail',
			name: 'HelpDetail',
			component: HelpDetail
		},
		{
			path: '/complain',
			name: 'Complain',
			component: Complain
		}
	]
})


