import SelectAddress from "@/pages/common/selectAddress";
import NavHeader from '@/components/navHeader/index';
import DateTimePicker from '@/components/dateTimePicker/index';
import { getNowFormatDate, changeTitle } from '@/common/common';
export default {
    components: { NavHeader, DateTimePicker, SelectAddress },
    data(){
        return{
            type: this.$route.query.type,
            showDateTimePicker: false,
            time: getNowFormatDate(),
            showSelectAddress:false,
            formData:{
                "uftaddr": "",
                "uftprice": 0,
                "uftstarttime": "",
                "uftphone": "",
                "uftlocation": "",
                "uftbrokeninfo": "",
                "uftspecialwith": ""
            }
        }
    },
    mounted(){
        changeTitle('维修预约');
    },
    methods:{
        selectTime(){
            this.showDateTimePicker = true;
        },
        getTime(time){
            this.time = time;
        },
        getAddressJson(addressJson){
            this.formData.uftaddr = addressJson.addressDetail+ addressJson.number+ addressJson.floor;
            this.formData.uftlocation = addressJson.location.lng+','+addressJson.location.lat;
        },
        // 提交数据
        submitData(){
            this.formData.uftstarttime = this.time.replace(/-/g,'').replace(/:/,'').replace(" ",'');
            if(!this.formData.uftaddr){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入地址",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.uftphone){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入手机号",
                    position:'middle'
                });
                return false;
            }
            // console.log(this.formData)
            this.$http({
                url: '/trade/fixer_appointment/',
                method: 'post',
                data: this.formData,
                params:{token: localStorage.getItem('token')}
            }).then(res=>{
                if(res.status == 200){
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                    this.$router.push({path:'/myOrder', query:{from:'repair'}})
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
            })
        }
    }
}



