import NavHeader from '@/components/navHeader/index';
import { changeTitle } from '@/common/common'
export default {
    props:['showSelectAddress', 'selectAddressType', 'serviceType'],
    components: { NavHeader },
    data(){
        return{
            selectCity: null,
            allCitys:[],
            address: {},
            address_detail: null, //详细地址
            userlocation: {lng: "", lat: ""},
        }
    },
    created(){
        this.getallCitys();
    },
    mounted(){
        changeTitle('选择地址');
        this.$nextTick(function () {

                var th = this
                // 创建Map实例
                var map = new BMap.Map("allmap");
                // 初始化地图,设置中心点坐标，
                var point = new BMap.Point(121.160724,31.173277); // 创建点坐标，汉得公司的经纬度坐标
                map.centerAndZoom(point, 15);
                map.enableScrollWheelZoom();
                var ac = new BMap.Autocomplete(    //建立一个自动完成的对象
                    {
                        "input": "suggestId"
                        , "location": map
                    });
                var myValue
                ac.addEventListener("onconfirm", function (e) {    //鼠标点击下拉列表后的事件
                    var _value = e.item.value;
                    if(th.allCitys.indexOf(_value.city)==-1){
                        th.$vux.toast.show({
                            type:'text',
                            text: "不在服务城市",
                            position:'middle'
                        });
                        th.address_detail = null;
                    }else{
                        myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
                        th.address_detail = myValue
                        setPlace();
                    }
                    
                });

                function setPlace() {
                    map.clearOverlays();    //清除地图上所有覆盖物
                    function myFun() {
                        th.userlocation = local.getResults().getPoi(0).point;    //获取第一个智能搜索的结果
                        th.$emit('getLocation',th.address_detail,th.userlocation);
                        map.centerAndZoom(th.userlocation, 18);
                        map.addOverlay(new BMap.Marker(th.userlocation));    //添加标注
                    }

                    var local = new BMap.LocalSearch(map, { //智能搜索
                        onSearchComplete: myFun
                    });
                    local.search(myValue);
                    

                    //测试输出坐标（指的是输入框最后确定地点的经纬度）
                    map.addEventListener("click",function(e){
                            //经度
                            //console.log(th.userlocation.lng);
                            //维度
                            //console.log(th.userlocation.lat);
                    })
                }

        })
   },
    methods:{
        cancel(){
            this.$emit('updateIsShow', false);
            this.address = {};
            this.address_detail = null;
            this.userlocation={lng: "", lat: ""};
        },
        submitAddress(){
            if(this.address_detail.indexOf('市')==-1||this.allCitys.indexOf(this.address_detail.slice(0,this.address_detail.indexOf('市')+1))==-1){
                this.$vux.toast.show({
                    type:'text',
                    text: "不在服务城市",
                    position:'middle'
                });
                this.address_detail = null;
                return false
            }
            this.address.addressDetail = this.address_detail;
            this.address.location = this.userlocation;
            if(!this.address.addressDetail){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入地址",
                    position:'middle'
                });
                return false;
            }
            if(this.serviceType!='room'){
                if(!this.address.number){
                    this.$vux.toast.show({
                        type:'text',
                        text: "请输入楼层",
                        position:'middle'
                    });
                    return false;
                }
                if(!this.address.floor){
                    this.$vux.toast.show({
                        type:'text',
                        text: "请输入楼号",
                        position:'middle'
                    });
                    return false;
                }
            }
            this.address.type= this.selectAddressType;
            this.$emit('getAddressJson',this.address);
            this.cancel();
        },
        getallCitys(){
            let url = '';
            if(this.serviceType == 'moveHouse'){
                url = '/mover/city_list/';
            }else if(this.serviceType == 'repair'){
                url = '/fixer/city_list/';
            }else if(this.serviceType == 'clean'){
                url="/cleaner/city_list/";
            }else{
                url="/room/get_city/";
            }
            this.$http.get(url).then(res=>{
                let arr = [];
                 res.data.citys.map((item, index)=>{
                     arr.push(item.name);
                 })
                this.allCitys = arr;
            })
        }
    }
}



