import NavHeader from '@/components/navHeader/index';
import NoData from '@/components/noData/index';
import { changeTitle } from '@/common/common'
export default {
    components: { NavHeader,NoData },
    data(){
        return{
            fromUrl: '',
            tabIndex: !this.$route.query.from?'all':this.$route.query.from=='clean'?'cleaner':this.$route.query.from=='repair'?'fixer':this.$route.query.from=='moverHouse'?'mover':'all',
            orderData:[],
            query:{//查询参数
                page: 1,
                count: 5,
                token: localStorage.getItem('token'),
                type:!this.$route.query.from?'all':this.$route.query.from=='clean'?'cleaner':this.$route.query.from=='repair'?'fixer':this.$route.query.from=='moverHouse'?'mover':'all',
            },
        }
    },
    beforeRouteEnter (to, from, next) {
        // 记录滚动条位置
        if(!sessionStorage.askPositon || from.path == '/home'){//当前页面刷新不需要切换位置
            sessionStorage.askPositon = '';
            next(vm=>{
                vm.query = {//查询参数
                    page: 1,
                    count: 5,
                    token: localStorage.getItem('token'),
                    type:!vm.$route.query.from?'all':vm.$route.query.from=='clean'?'cleaner':vm.$route.query.from=='repair'?'fixer':this.$route.query.from=='moverHouse'?'mover':'all',
                }
                vm.fromUrl = '/'+ vm.tabIndex=='mover'?'moveHouse':vm.tabIndex=='fixer'?'repair':vm.tabIndex=='cleaner'?'clean':'home';
                vm.getData(vm.query, ()=>{});
            });
        }else{
            next(vm => {
                if(vm && vm.$refs.my_scroller){//通过vm实例访问this
                    setTimeout(function () {
                        vm.$refs.my_scroller.scrollTo(0, sessionStorage.askPositon, 0);
                    },5)//同步转异步操作
                }
            })
        }
    },
    beforeRouteLeave(to,from,next){//记录离开时的位置
        sessionStorage.askPositon = this.$refs.my_scroller && this.$refs.my_scroller.getPosition() && this.$refs.my_scroller.getPosition().top;
        next();
    },
    created(){
        // 返回哪个页面
        this.fromUrl = '/'+ this.tabIndex=='mover'?'moveHouse':this.tabIndex=='fixer'?'repair':this.tabIndex=='cleaner'?'clean':'home';
        this.getData(this.query,()=>{});
    },
    mounted(){
        changeTitle('我的订单');
    },
    methods:{
        pay (type,item) {
            let json = {};
            json[type] = item[type];
            this.$http({
                url: '/pay/pay_for_service/',
                method: 'post',
                params:{ token: localStorage.getItem('token') },
                data:json
            }).then(res=>{
                if(res.status == 200){
                    let vm = this
                    if (typeof WeixinJSBridge === 'undefined') {
                        if (document.addEventListener) {
                            document.addEventListener('WeixinJSBridgeReady', vm.onBridgeReady(res.message,type), false)
                        } else if (document.attachEvent) {
                            document.attachEvent('WeixinJSBridgeReady', vm.onBridgeReady(res.message,type))
                            document.attachEvent('onWeixinJSBridgeReady', vm.onBridgeReady(res.message,type))
                        }
                    } else {
                        vm.onBridgeReady(res.message,type)
                    }
                }
            })
        },
        onBridgeReady (data,type) {
            let self = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    'appId': data.appId,
                    'timeStamp': String(data.timeStamp),    //这里必须要转换为字符串。ios跟android表现不同。后台返回的是数值，但是微信方面必须要json参数都是字符串形式，android会自动转换成字符串（当时我在这里也找了很久的博文才知道的）
                    'nonceStr': data.nonceStr,
                    'package': data.package,
                    'signType': data.signType,
                    'paySign': data.paySign
                },
                function (res) {
                    if (res.err_msg === 'get_brand_wcpay_request:ok') {
                        self.$vux.toast.show({
                            type:'text',
                            text: "支付成功",
                            position:'middle'
                        });
                        if(type=="uftid"){
                            self.$router.push('/fixerOrderDetail?uftid='+data.uftid+'&type=fixer');
                        }else if(type=="uctid"){
                            self.$router.push('/cleanerOrderDetail?uctid='+data.uctid+'&type=cleaner');
                        }else if(type=="umtid"){
                            self.$router.push('/moverOrderDetail?umtid='+data.umtid+'&type=mover');
                        }
                    } else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
                        self.$vux.toast.show({
                            type:'text',
                            text: "支付取消",
                            position:'middle'
                        })
                    } else {
                        self.$vux.toast.show({
                            type:'text',
                            text: "支付失败",
                            position:'middle'
                        })
                    }
                }
            )
        },
        seeDetail(data){
            if(data.type=="fixer"){
                this.$router.push('/fixerOrderDetail?uftid='+data.uftid+'&type=fixer');
            }else if(data.type=="cleaner"){
                this.$router.push('/cleanerOrderDetail?uctid='+data.uctid+'&type=cleaner');
            }else if(data.type=="mover"){
                this.$router.push('/moverOrderDetail?umtid='+data.umtid+'&type=mover');
            }
        },
        selectTab(index){
            this.tabIndex = index;
            this.query.type = index;
            this.query.page = 1;
            this.orderData= [];
            if(this.query.type=='all'){
                delete this.query.type
            }
            this.$refs.my_scroller.scrollTo(0, 0, 0);
            this.getData(this.query,()=>{});
            // 返回哪个页面
            this.fromUrl = '/'+ this.tabIndex=='mover'?'moveHouse':this.tabIndex=='fixer'?'repair':this.tabIndex=='cleaner'?'clean':'home';
        },
        getData(query, done){
            this.$http.get('/trade/get_my_appiontlist/',{params:query}).then(res=>{
                if(res.status == 200){
                    if(res.data.length>0){
                        this.orderData = res.data;
                        done();
                    }else{
                        this.orderData = [];
                    }
                }
            })
        },
        // 刷新
        refresh(done){
            this.query.page = 1;
            this.getData(this.query,done);
        },
        // 加载数据
        infinite(done){
            this.query.page++;
            this.$http.get('/trade/get_my_appiontlist/',{params: this.query}).then(res=>{
                if(res.data.length>0){
                    this.orderData = this.orderData.concat(res.data);
                    done()
                }else{
                    done(true)
                }
            })
        }

    }
}



