import Vue from 'vue'
import App from './App'
import router from './router'


import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

Vue.use(AjaxPlugin);Vue.use(ToastPlugin);Vue.use(WechatPlugin);
import VueScroller from 'vue-scroller'
Vue.use(VueScroller);

Vue.prototype.$http.defaults.baseURL ="https://ljgj.lkfcni.cn/api";
Vue.prototype.$http.defaults.timeout = 15 * 1000;
Vue.prototype.$http.defaults.headers = { 'Content-Type': 'application/json' };
Vue.prototype.$http.interceptors.response.use(function(res){

return res.data
},function(err){
Vue.$vux.toast.show({
type:'text',
text: "请求出错",
position:'middle'
})
return Promise.reject(err);
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



