
import jsonp from 'jsonp'
import NavHeader from '@/components/navHeader/index';
import NoData from '@/components/noData/index';
import Icon from 'vux/src/components/icon/index.vue'

import { changeTitle, wxConfig } from '@/common/common';
export default {
components: { NavHeader, NoData, Icon },
data(){
return {
cityClean: localStorage.getItem('cityClean')?localStorage.getItem('cityClean'):'定位中',
cityCodeClean:'',
cleanData: [],
showSelectCity: false,
cityList: []
}
},
mounted(){
changeTitle('邻家保洁');
wxConfig(['getLocation']);
if(!localStorage.getItem('cityClean')){
this.getLocation();
}else{
this.getData();
}
},
methods:{
getLocation(){
var _this = this;
this.$wechat.ready(()=>{
this.$wechat.getLocation({
type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
if (err) {
console.error(err.message);
} else {
_this.cityClean=data.result.addressComponent.city;
_this.cityCodeClean = data.result.addressComponent.adcode.slice(0,4)+'00';
localStorage.setItem('cityClean',_this.cityClean);
localStorage.setItem('cityCodeClean',_this.cityCodeClean);
_this.getData();
}
})
},
fail: function (err) {
_this.$vux.toast.show({
type:'text',
text: "定位失败",
position:'middle'
})
}
})
})
},
getData(){
this.$http.get('/cleaner/clean_list/',{params:{city_id:localStorage.getItem('cityCodeClean')}}).then(res=>{
if(res.status == 200){
this.cleanData = res.data.cleans;
}
})
},
checkMove(path){
if(localStorage.getItem('token')){
this.$router.push(path);
}else{
this.$router.push({path:'/login',query:{from:'clean'}})
}

},
toMyOrder(){
if(!localStorage.getItem('token')){
this.$router.push({path:'/login',query:{from:'myOrder'}})
}else{
this.$router.push({path:'/myOrder',query:{from:'clean'}})
}
},
selectCity(){
this.showSelectCity = true;
this.$http.get('/cleaner/city_list/').then(res=>{
if(res.status == 200){
this.cityList = res.data.citys;
}else{
this.cityList = [];
}
})
},
changeCity(item){
this.showSelectCity = false;
localStorage.setItem('cityClean',item.name);
this.cityClean = item.name;
localStorage.setItem('cityCodeClean',item.city_id);
this.getData();
}
}
}



