export default {
    data(){
        return {
            guideList:[]
        }
    },
    created(){
        this.getGuide();
    },
    methods:{
        getGuide(){
            this.$http.get('/guide/get_list/').then(res=>{
                if(res.status == 200){
                    this.guideList = res.data.guides;
                }else{
                    this.guideList = [];
                }
            })
        }
    }
}



