import NavHeader from '@/components/navHeader/index';
import { changeTitle } from '@/common/common';
export default {
    components: { NavHeader },
    data(){
        return{
            cancelText:'确定取消订单？',
            isShowCancelOrder: false,
            formData:{}
        }
    },
    created(){
        this.getData();
    },
    mounted(){
        changeTitle('订单详情');
    },
    methods:{
        applicationDrawback(){
            this.cancelText = '确定申请退款？';
            this.isShowCancelOrder = true;
        },
        cancelDW(){
            this.cancelText = '确定取消订单？';
            this.isShowCancelOrder = true;
        },
        // 取消订单
        cancelOrder(){
            let json =  {uctid: this.$route.query.uctid};
            let url = '/trade/user_cancle_cleanerorder/';
            this.$http({
                url:url,
                method: 'post',
                data: json,
                params:{token:localStorage.getItem('token')}
            }).then(res => {
                if(res.status == 200){
                    this.getData();
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
                this.isShowCancelOrder = false;
            })
        },
        filterTime(time){
            if(time){
                return time.slice(0,4)+'-'+time.slice(4,6)+'-'+time.slice(6,8)+' '+ time.slice(8,10)+':'+ time.slice(10,12);
            }
        },
        getData(){
            this.$http.get('/trade/get_appointment_one/?token='+localStorage.getItem('token')+'&uctid='+this.$route.query.uctid
            ).then(res => {
                if(res.status == 200){
                    this.formData = res.data.order;
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
            })
        }
    }
}



