
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { changeTitle, goTop } from '@/common/common'
export default {
components: {
Swiper, SwiperItem
},
data(){
return{
location:[],
imgList:[],
houseData:{
house:{},
equirment:{},
villege:{},
area:{}
},
keeper:{},
playState: 'pause',
showPause: false,
timer: null
}
},
watch:{
location(){
setTimeout(()=>{
this.getLocation(this.location[0],this.location[1])
},1000);
}
},
created(){
this.getData();
this.getHouseKeeper();
},
mounted(){
changeTitle('房源详情');
goTop();
},
methods:{

moverShowPause(){
this.showPause = true;
clearTimeout(this.timer);
this.timer = setTimeout(()=>{
this.showPause = false;
},1000);
},

play(ref){
if(this.playState == 'pause'){
document.getElementById(ref).play();
this.playState = 'play';
}else{
document.getElementById(ref).pause();
this.playState = 'pause';
}
},

getHouseKeeper(){
this.$http.get('/user/get_one_housekeeper/').then(res=>{
if(res.status == 200){
this.keeper = res.data.housekeeper;
}
});
},

getLocation(j,w){
this.$nextTick(function () {
var th = this

var map = new BMap.Map("allmap");

var point = new BMap.Point(121.160724,31.173277); 
map.centerAndZoom(point, 19);
map.enableScrollWheelZoom();

function doLocate(jd, wd) {//根据经纬度定位
var point = new BMap.Point(jd, wd);
var marker = new BMap.Marker(point);
map.addOverlay(marker);

marker.enableDragging(); 
marker.addEventListener('dragend', function (e) {//拖动标注结束
var pointNew = e.point;
});
map.centerAndZoom(point, 19);

}
doLocate(j,w);
})
},
getData(){
this.$http.get('/room/get_detail/',{params:{roid: this.$route.query.id}}).then(res=>{
if(res.status == 200){
res.data.media.map((item,index)=>{
this.imgList.push({url:'javascript:;', img: item.repic})
})
this.houseData = res.data;
this.location = res.data.villege?[res.data.villege.location.split(',')[0], res.data.villege.location.split(',')[1]]:[0,0];
}
})
}
}
}



