import SelectAddress from "@/pages/common/selectAddress";
import NavHeader from '@/components/navHeader/index';
import DateTimePicker from '@/components/dateTimePicker/index';
import DurationPicker from '@/components/durationPicker/index';
import { getNowFormatDate, changeTitle } from '@/common/common';
export default {
    components: { NavHeader, DateTimePicker, DurationPicker, SelectAddress },
    data(){
        return{
            type: this.$route.query.type,
            showDateTimePicker: false,
            showDurationPicker: false,
            showSelectAddress:false,
            time: getNowFormatDate(),
            formData:{
                "sceid": this.$route.query.id,
                "uctpreviewstarttime": "",
                "uctaddr": "",
                "uctpreviewlastingtime": 1,
                "uctphone": "",
                "uctspecialwish": "",
                "uctprice": "",
                "uctlocation": ""
            }
        }
    },
    mounted(){
        changeTitle('保洁预约');
    },
    methods:{
        totalPrice(){
            return this.formData.uctpreviewlastingtime * this.$route.query.price
        },
        selectTime(){
            this.showDateTimePicker = true;
        },
        getTime(time){
            this.time = time;
        },
        selectDuration(){
            this.showDurationPicker = true;
        },
        getDuration(duration){
            this.formData.uctpreviewlastingtime = duration;
        },
        // 获取搬家服务详情
        getDetail(){
            this.$http.get('/mover/get_detail/',{params:{smsid:this.$route.query.id}}).then(res=>{
                if(res.status == 200){
                    this.moveHouseData = res.data.detail;
                }
            })
        },
        getAddressJson(addressJson){
            this.formData.uctaddr = addressJson.addressDetail+ addressJson.number+ addressJson.floor;
            this.formData.uctlocation = addressJson.location.lng+','+addressJson.location.lat;
        },
        // 提交数据
        submitData(){
            this.formData.uctprice = this.totalPrice();
            this.formData.uctpreviewstarttime = this.time.replace(/-/g,'').replace(/:/,'').replace(" ",'');
            if(!this.formData.uctaddr){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入地址",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.uctphone){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入手机号",
                    position:'middle'
                });
                return false;
            }
            // console.log(this.formData)
            this.$http({
                url: '/trade/cleaner_apponintment/',
                method: 'post',
                data: this.formData,
                params:{token: localStorage.getItem('token')}
            }).then(res=>{
                if(res.status == 200){
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                    this.$router.push({path:'/myOrder', query:{from:'clean'}})
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
            })
        }
    }
}



