export default {
    data(){
        return{
            showActiveIndex: [],
            decorateActiveIndex:[]
        }
    },
    methods:{
        // 筛选
        activeTab(array, index){
            if(array.indexOf(index)==-1){
                array.push(index);
            }else{
                array.splice(array.indexOf(index),1);
            }
        },
        // 传递数据
        submitData(){
            let filterData = {
            };
            this.showActiveIndex.join('|')&&(filterData.show_type = this.showActiveIndex.join('|'));
            this.decorateActiveIndex.join('|')&&(filterData.style = this.decorateActiveIndex.join('|'));
            this.$emit('updateIsShow',false);
            this.$emit('getRentOtherFilter',filterData);
        },
        resetData(){
            this.showActiveIndex = [];
            this.decorateActiveIndex= [];
            let filterData = {
                show_type: null,
                style: null
            };
            this.$emit('updateIsShow',false);
            this.$emit('getRentOtherFilter',filterData);
        }
    }
}



