import YearPicker from './datePicker/index'
import MonthPicker from './datePicker/index'
import DatePicker from './datePicker/index'
import HourPicker from './datePicker/index'
export default {
    props:['defaultTime'],
    name: 'DateTimePicker',
    components: { YearPicker, MonthPicker, DatePicker, HourPicker },
    data(){
        return{
            year: null,
            month: null,
            date: null,
            hour: null,
            yearFilter: [],
            monthFilter: [],
            dateFilter: [],
            hourFilter: []
        }
    },
    watch:{
        year(){
            this.getDateFilter(this.year,this.month);
        },
        month(){
            this.getDateFilter(this.year,this.month);
        }
    },
    created(){
        this.year = this.defaultTime.split(' ')[0].split('-')[0];
        this.month = this.defaultTime.split(' ')[0].split('-')[1];
        this.date = this.defaultTime.split(' ')[0].split('-')[2];
        this.hour = this.defaultTime.split(' ')[1].split(':')[0];
        this.getYearFilter();
        this.getMonthFilter();
        this.getDateFilter(this.year,this.month);
        this.getHourFilter();
    },
    methods:{
        cancel(){
            this.$emit('updateIsShow', false)
        },
        confirm(){
            this.$emit('getTime', this.year+'-'+this.month+'-'+this.date+' '+this.hour+':00')
            this.$emit('updateIsShow', false);
        },
        getYearFilter(){
            for(let i= 0; i<10; i++){
                this.yearFilter.push((new Date().getFullYear()+i).toString());
            }
        },
        getMonthFilter(){
            this.monthFilter = [];
            for(let i= 1; i<13; i++){
                if(i<10){
                    this.monthFilter.push('0'+i);
                }else{
                    this.monthFilter.push(i.toString());
                }
            }
        },
        getDateFilter(year,month){
            this.dateFilter = [];
            if(((year % 4 == 0 && year % 100 != 0) || year % 400 == 0)&&month=="02"){
                for(let i= 1; i<29; i++){
                    if(i<10){
                        this.dateFilter.push('0'+i);
                    }else{
                        this.dateFilter.push(i.toString());
                    }
                }
            }else if(!((year % 4 == 0 && year % 100 != 0) || year % 400 == 0)&&month=="02"){
                for(let i= 1; i<30; i++){
                    if(i<10){
                        this.dateFilter.push('0'+i);
                    }else{
                        this.dateFilter.push(i.toString());
                    }
                }
            }else if(month=="01"||month=="03"||month=="05"||month=="07"||month=="08"||month=="10"||month=="12"){
                for(let i= 1; i<32; i++){
                    if(i<10){
                        this.dateFilter.push('0'+i);
                    }else{
                        this.dateFilter.push(i.toString());
                    }
                }
            }else{
                for(let i= 1; i<31; i++){
                    if(i<10){
                        this.dateFilter.push('0'+i);
                    }else{
                        this.dateFilter.push(i.toString());
                    }
                }
            }
            
        },
        getHourFilter(){
            this.hourFilter = [];
            for(let i= 0; i<24; i++){
                if(i<10){
                    this.hourFilter.push('0'+i);
                }else{
                    this.hourFilter.push(i.toString());
                }
            }
        },
        getYearValue(year){
            this.year = year;
        },
        getMonthValue(month){
            this.month = month;
        },
        getDateValue(date){
            this.date = date;
        },
        getHourValue(hour){
            this.hour = hour;
        }
    }
}



