import jsonp from 'jsonp'
import NavHeader from '@/components/navHeader/index';
import { changeTitle, pinyin, wxConfig } from '@/common/common'
export default {
    components: { NavHeader },
    data(){
        return{
            city: '定位中',
            cityCode: '',
            selectCityCode: localStorage.getItem('cityCode'),
            fromUrl: '',
            hotCitys: [],
            allCitys: []
        }
    },
    created(){
        this.fromUrl = '/'+this.$route.query.from;
        this.getAllCities();
    },
    mounted(){
        changeTitle('选择城市');
        wxConfig(['getLocation']);
        this.getLocation();
    },
    methods:{
        getAllCities(){
            this.$http.get('/room/get_city/').then(res=>{
                if(res.status == 200){
                    this.hotCitys = res.data.citys;
                    res.data.citys.map((item,index)=>{
                        if(index==0){
                            this.allCitys.push({ initial: pinyin.getCamelChars(item.name.slice(0,1)), citys: [{city_id: item.city_id, initial: pinyin.getCamelChars(item.name.slice(0,1)), name: item.name}]})
                        }else{
                            if(this.allCitys.find((it,ix)=>it.initial == pinyin.getCamelChars(item.name.slice(0,1)))){
                                this.allCitys.find((it,ix)=>it.initial == pinyin.getCamelChars(item.name.slice(0,1))).citys.push({city_id: item.city_id, initial: pinyin.getCamelChars(item.name.slice(0,1)), name: item.name})
                            }else{
                                this.allCitys.push({ initial: pinyin.getCamelChars(item.name.slice(0,1)), citys: [{city_id: item.city_id, initial: pinyin.getCamelChars(item.name.slice(0,1)), name: item.name}]})
                            }
                        }
                    });
                }
            })
        },
        resetLocation(){
            var _this = this;
            this.$wechat.ready(()=>{
                this.$wechat.getLocation({
                    type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                    success: function (res) {
                        jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
                            if (err) {
                                console.error(err.message);
                            } else {
                                _this.city=data.result.addressComponent.city;
                                _this.cityCode = data.result.addressComponent.adcode.slice(0,4)+'00';
                                localStorage.setItem('city',_this.city);
                                localStorage.setItem('cityCode',_this.cityCode);
                                _this.selectCityCode = data.result.addressComponent.adcode.slice(0,4)+'00';
                                _this.$router.push('/'+_this.$route.query.from);
                            }
                        })
                    },
                    fail: function (err) {
                        _this.$vux.toast.show({
                            type:'text',
                            text: "定位失败",
                            position:'middle'
                        })
                    }
                })
            })
        },
        // 定位
        getLocation(){
            var _this = this;
            this.$wechat.ready(()=>{
                this.$wechat.getLocation({
                    type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                    success: function (res) {
                        jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
                            if (err) {
                                console.error(err.message);
                            } else {
                                _this.city=data.result.addressComponent.city;
                                _this.cityCode = data.result.addressComponent.adcode.slice(0,4)+'00';
                            }
                        })
                    },
                    fail: function (err) {
                        _this.$vux.toast.show({
                            type:'text',
                            text: "定位失败",
                            position:'middle'
                        })
                    }
                })
            })
        },
        // 选择城市
        selectCity(city){
            localStorage.setItem('city',city.name);
            localStorage.setItem('cityCode',city.city_id);
            this.$router.push('/'+this.$route.query.from);
        }
    }
}



