
import jsonp from 'jsonp'
import NavHeader from '@/components/navHeader/index';
import NoData from '@/components/noData/index';
import Icon from 'vux/src/components/icon/index.vue'

import { changeTitle, wxConfig } from '@/common/common';
export default {
components: { NavHeader, NoData, Icon },
data(){
return {
moveHouseData: [],
cityMove: localStorage.getItem('cityMove')?localStorage.getItem('cityMove'):'定位中',
cityCodeMove:'',
showSelectCity: false,
cityList: []
}
},
created(){
this.getData();
},
mounted(){
changeTitle('邻家搬家');
wxConfig(['getLocation']);
if(!localStorage.getItem('cityMove')){
this.getLocation();
}else{
this.getData();
}
},
methods:{
getLocation(){
var _this = this;
this.$wechat.ready(()=>{
this.$wechat.getLocation({
type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
if (err) {
console.error(err.message);
} else {
_this.cityMove=data.result.addressComponent.city;
_this.cityCodeMove = data.result.addressComponent.adcode.slice(0,4)+'00';
localStorage.setItem('cityMove',_this.cityMove);
localStorage.setItem('cityCodeMove',_this.cityCodeMove);
_this.getData();
}
})
},
fail: function (err) {
_this.$vux.toast.show({
type:'text',
text: "定位失败",
position:'middle'
})
}
})
})
},
getData(){
this.$http.get('/mover/move_list/',{params:{city_id:localStorage.getItem('cityCodeMove')}}).then(res=>{
if(res.status == 200){
this.moveHouseData = res.data.movers;
}
})
},
checkMove(path){
if(localStorage.getItem('token')){
this.$router.push(path);
}else{
this.$router.push({path:'/login',query:{from:'moveHouse'}})
}

},
toMyOrder(){
if(!localStorage.getItem('token')){
this.$router.push({path:'/login',query:{from:'myOrder'}})
}else{
this.$router.push({path:'/myOrder',query:{from:'moveHouse'}})
}
},
selectCity(){
this.showSelectCity = true;
this.$http.get('/mover/city_list/').then(res=>{
if(res.status == 200){
this.cityList = res.data.citys;
}else{
this.cityList = [];
}
})
},
changeCity(item){
this.showSelectCity = false;
localStorage.setItem('cityMove',item.name);
this.cityMove = item.name;
localStorage.setItem('cityCodeMove',item.city_id);
this.getData();
}
}
}



