import SelectCol from '@/components/SelectCol/index.vue';
export default {
    components: { SelectCol },
    data(){
        return{
            locationActiveIndex: 0,
            scrollFilterArea: [],//区域名称
            areaList: [],//区域信息
            scrollFilterSubway:[],//地铁名称
            subwayList:[],//地铁信息
            scrollFilterSubwayPosition:[],//地铁站名称
            subwayPositionList:[],//地铁站信息,
            subwayPosition: null,
            areaId: null
        }
    },
    created(){
        this.getArea();
        this.getSubway();
    },
    methods:{
        getSelectValue(value) {
            this.areaId = this.areaList.find(item=>item.name == value)['area_id']
        },
        getSelectValueSubway(value) {
           let id = this.subwayList.find(item=>item.linename==value)['subwaylineid'];
            this.getSubwayPosition(id);
        },
        getSelectValueSubwayPosition(value) {
            this.subwayPosition = value;
        },
        // 获取城市的区
        getArea(){
            this.$http.get('/room/get_area_by_cityid/', {params:{'city_id':localStorage.getItem('cityCode')}}).then(res=>{
                res.data.area_list.map((item, index)=>{
                    this.scrollFilterArea.push(item.name);
                })
                this.areaList = res.data.area_list;
                this.areaId = res.data.area_list[0].area_id;
            })
        },
        // 获取地铁路线
        getSubway(){
             this.$http.get('/room/get_subway/', {params:{'city_id':localStorage.getItem('cityCode')}}).then(res=>{
                res.data.map((item, index)=>{
                    this.scrollFilterSubway.push(item.linename);
                })
                this.subwayList = res.data;
                this.getSubwayPosition(res.data[0].subwaylineid)
            })
        },
        // 获取地铁站
        getSubwayPosition(id){
            this.scrollFilterSubwayPosition = [];//地铁站名称
            this.subwayPositionList = [];//地铁站信息
             this.$http.get('/room/get_position/', {params:{'line_id':id}}).then(res=>{
                res.data.map((item, index)=>{
                    this.scrollFilterSubwayPosition.push(item.position);
                })
                this.subwayPositionList = res.data;
                this.subwayPosition = res.data[0].position;
            })
        },
        // 位置切换
        selectLocationType(index){
            this.locationActiveIndex = index;
        },
        // 传递数据
        submitData(){
            let filterData = {};
            if(this.locationActiveIndex==1){
                filterData.subway=this.subwayPosition;
                filterData.area_id=null;
            }else{
                filterData.area_id=this.areaId;
                filterData.subway=null;
            }
            this.$emit('updateIsShow',false);
            this.$emit('getRentLocationFilter',filterData);
        },
        resetData(){
            this.locationActiveIndex = 0;
            this.subwayPosition = null;
            this.areaId = null;
            let filterData = {
                subway: null,
                area_id: null
            };
            this.$emit('updateIsShow',false);
            this.$emit('getRentLocationFilter',filterData);
        }
    }
}



