import SelectAddress from "@/pages/common/selectAddress";
import NavHeader from '@/components/navHeader/index';
import { getNowFormatDate, changeTitle } from '@/common/common';
export default {
    components: { NavHeader, SelectAddress },
    data(){
        return{
            type: this.$route.query.type,
            showSelectAddress: false,
            formData:{
                "usercomplainttext": "",
                "usercomplaintaddress": "",
                "usercomplaintphone": ''
            }
        }
    },
    mounted(){
        changeTitle('投诉');
    },
    methods:{
        getAddressJson(addressJson){
            this.formData.usercomplaintaddress = addressJson.addressDetail;
        },
        // 提交数据
        submitData(){
            if(!this.formData.usercomplainttext){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入投诉内容",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.usercomplaintaddress){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入地址",
                    position:'middle'
                });
                return false;
            }
            if(!this.formData.usercomplaintphone){
                this.$vux.toast.show({
                    type:'text',
                    text: "请输入手机号",
                    position:'middle'
                });
                return false;
            }
            this.$http({
                url: '/trade/add_complaint/',
                method: 'post',
                data: this.formData,
                params:{token: localStorage.getItem('token')}
            }).then(res=>{
                if(res.status == 200){
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }else{
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    });
                }
            })
        }
    }
}



