
import jsonp from 'jsonp'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import HomeList from '@/components/homeList/index';
import { changeTitle, wxConfig } from '@/common/common'

export default {
components: {
Swiper, SwiperItem, HomeList
},
data () {
return {
isLogin: localStorage.getItem('token'),
city: localStorage.getItem('city')?localStorage.getItem('city'):'定位中',
cityCode:'',
swiperList:[],
detailData:{},
showBtns:false
}
},
created(){
this.getBanner();
this.getData();
},
mounted(){
changeTitle('首页');
wxConfig(['getLocation']);
if(!localStorage.getItem('city')){
this.getLocation();
}
},
methods:{
logout(){
localStorage.clear();
this.showBtns = false;
this.getLocation();
},
goFilter(){
this.$router.push('/friendsFamily?from=home')
},
getLocation(){
var _this = this;
this.$wechat.ready(()=>{
this.$wechat.getLocation({
type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
if (err) {
console.error(err.message);
} else {
_this.city=data.result.addressComponent.city;
_this.cityCode = data.result.addressComponent.adcode.slice(0,4)+'00';
localStorage.setItem('city',_this.city);
localStorage.setItem('cityCode',_this.cityCode);
}
})
},
fail: function (err) {
_this.$vux.toast.show({
type:'text',
text: "定位失败",
position:'middle'
})
}
})
})
},

getBanner(){
this.$http.get('/index/banner/').then(res=>{
if(res.status==200){
let arr = [];
res.data.map((item,inx)=>{
arr.push({img: item.ibimage, url:'javascript:;'})
});
this.swiperList = arr;
}
})
},

getData(){
this.$http.get('/index/detail/').then(res=>{
if(res.status == 200){
let rent = res.data.join_rent.concat(res.data.whole_rent);
this.detailData = res.data;
this.detailData.rent = rent;
}
});
},
toMyOrder(){
if(!localStorage.getItem('token')){
this.$router.push({path:'/login',query:{from:'myOrder'}})
}else{
this.$router.push({path:'/myOrder',query:{from:'home'}})
}
}
}
}



