
import Swiper from 'vux/src/components/swiper/swiper.vue'

import NavHeader from '@/components/navHeader/index';
export default {
components: { NavHeader, Swiper },
data () {
return {
tabIndex: 0,
swiperList:[],
}
},
created(){
this.getBanner();
},
mounted(){

},
methods:{
tabClick(index){
this.tabIndex = index;
},

getBanner(){
this.$http.get('/room/get_homestay_banner/').then(res=>{
if(res.status==200){
let arr = [];
res.data.map((item,inx)=>{
arr.push({img: item.hsbimage, url:'javascript:;'})
});
this.swiperList = arr;
}
})
},
}
}



