import NavHeader from '@/components/navHeader/index';
export default {
    components: { NavHeader },
    data(){
        return{
            cityName: localStorage.getItem('city'),
            formData:{
                "phacity": "",
                "phavillege": "",
                "phaphone": "",
                "phaname": ""
            }
        }
    },
    methods:{
        submitData(){
            this.$http({
                url:'/trade/add_providehouse_apply/',
                method:'post',
                data: this.formData,
                params:{'token':localStorage.getItem('token')}
            }).then(res=>{
                if(res.status == 200){
                    this.$vux.toast.show({
                        type:'text',
                        text: res.message,
                        position:'middle'
                    })
                }
            })
        }
    }
}



