import jsonp from 'jsonp'
import HomeItem from '@/components/homeItem/index';
import NavHeader from '@/components/navHeader/index';
import RentLocation from '../friendsFamily/components/rentLocation';
import RentPrice from './components/rentPrice';
import RentOther from '../friendsFamily/components/rentOther';
import MainMap from '@/components/map/index';
import { changeTitle, wxConfig, goTop } from '@/common/common';
export default {
    components: { HomeItem, NavHeader, MainMap, RentLocation, RentPrice, RentOther },
    data () {
        return {
            /* 位置筛选 */
            showRentLocation: false,
            rentLocationFilter: {},
            locationName:'<span>位置</span>',
            /* 价格筛选 */
            showRentPrice: false,
            rentPriceFilter: {},
            priceName:'<span>价格</span>',
            /* 其他筛选 */
            showRentOther: false,
            rentOtherFilter: {},

            homestayData:[],
            city: localStorage.getItem('city')?localStorage.getItem('city'):'定位中',
            cityCode:'',
            filterIndex: -1,
            showMap: false,
            showRentMethod: false,
            query:{//查询参数
                page: 1,
                count: 5,
                type: '3',
                city_id: localStorage.getItem('cityCode')
            },
            areaList:[],
            otherQuery:{
                page: 1,
                count: 5,
                type: 0,
                city_id: localStorage.getItem('cityCode'),
                subway: null,
                area_id: null,
                lowprice: null,
                highprice: null
            },
        }
    },
    watch:{
        'otherQuery':{
            handler:function(val,oldVal){
                this.locationName = this.query.subway&&!this.query.area_id?'<span class="filter-color">'+this.query.subway+'</span>':!this.query.subway&&this.query.area_id?'<span class="filter-color">'+this.areaList.find(item=>item.area_id==this.query.area_id)['name']+'</span>':'<span>位置</span>';
                this.priceName = this.query.lowprice&&this.query.highprice?'<span class="filter-color">'+this.query.lowprice+'-'+this.query.highprice+'</span>':!this.query.lowprice&&this.query.highprice?'<span class="filter-color">'+this.query.highprice+'以下</span>':this.query.lowprice&&!this.query.highprice?'<span class="filter-color">'+this.query.lowprice+'以上</span>':'<span>价格</span>';
            },
            deep: true
        }
    },
    created(){
        // this.getArea();
        // this.getData(this.query, ()=>{});
    },
    mounted(){
        changeTitle('邻家民宿');
        wxConfig(['getLocation']);
        if(!localStorage.getItem('city')){
            this.getLocation();
        }else{
            this.getArea();
            this.getData(this.query, ()=>{});
        }
    },
    beforeRouteEnter (to, from, next) {
        // 记录滚动条位置
        if(!sessionStorage.askPositon || from.path == '/home' || from.path == '/selectCity'){//当前页面刷新不需要切换位置
            sessionStorage.askPositon = '';
            next(vm=>{
                vm.showRentLocation = false;
                vm.showRentPrice = false;
                vm.showRentOther = false;
                vm.query = {//查询参数
                    page: 1,
                    count: 5,
                    type: '3',
                    city_id: localStorage.getItem('cityCode')
                },
                vm.otherQuery = {
                    page: 1,
                    count: 5,
                    type: 0,
                    city_id: localStorage.getItem('cityCode'),
                    subway: null,
                    area_id: null,
                    lowprice: null,
                    highprice: null
                };
                vm.city = localStorage.getItem('city');
                if(localStorage.getItem('city')){
                    vm.getArea();
                    vm.getData(vm.query, ()=>{});
                }
            });
        }else{
            next(vm => {
                if(vm && vm.$refs.my_scroller){//通过vm实例访问this
                    setTimeout(function () {
                        vm.$refs.my_scroller.scrollTo(0, sessionStorage.askPositon, 0);
                    },5)//同步转异步操作
                }
            })
        }
    },
    beforeRouteLeave(to,from,next){//记录离开时的位置
        sessionStorage.askPositon = this.$refs.my_scroller && this.$refs.my_scroller.getPosition() && this.$refs.my_scroller.getPosition().top;
        next();
    },
    methods:{
        // 定位
        getLocation(){
            var _this = this;
            this.$wechat.ready(()=>{
                this.$wechat.getLocation({
                    type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                    success: function (res) {
                        jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location='+res.latitude+','+res.longitude+'&output=json&pois=1&ak=1cYUunsIiDVKnMoGqXxZG5c6hw8sUsEL', null, (err, data) => {
                            if (err) {
                                console.error(err.message);
                            } else {
                                _this.city=data.result.addressComponent.city;
                                _this.cityCode = data.result.addressComponent.adcode.slice(0,4)+'00';
                                localStorage.setItem('city',_this.city);
                                localStorage.setItem('cityCode',_this.cityCode);
                                _this.query.page=1;
                                _this.query.city_id=_this.cityCode;
                                _this.getArea();
                                _this.getData(_this.query, ()=>{});
                            }
                        })
                    }
                })
            })
        },
        // 获取城市的区
        getArea(){
            this.$http.get('/room/get_area_by_cityid/', {params:{'city_id':localStorage.getItem('cityCode')}}).then(res=>{
                if(res.status==200)
                this.areaList = res.data.area_list;
            })
        },
        // 筛选切换
        filterTab(index){
            if(index == 1){
                this.showRentPrice =false;
                this.showRentOther = false;
                this.showRentLocation = !this.showRentLocation;
            }else if(index == 2){
                this.showRentLocation = false;
                this.showRentOther = false;
                this.showRentPrice = !this.showRentPrice;
            }else if(index == 3){
                this.showRentLocation = false;
                this.showRentPrice = false;
                this.showRentOther = !this.showRentOther;
            }
        },
        // 筛选
        getFilter(data){
            this.$refs.my_scroller.scrollTo(0, 0, 0);
            goTop();
            this.query.page = 1;
            for(let key in data){
                this.otherQuery[key] = data[key];//用于检测变化；
                if(data[key]!==null){
                    this.query[key] = data[key];
                }else{
                    delete this.query[key];
                }
            }
            this.getData(this.query, ()=>{});
        },
        getMapLocation(villege){
            this.query.page = 1;
            this.query.villege = villege;
            this.getData(this.query, ()=>{});
        },
        // 选择城市
        selectCity(){
            this.$router.push({path: '/selectCity', query:{ from: 'homestay'}})
        },
        getData(query, done){
            this.$http.get('/room/get_list/',{params:query}).then(res=>{
                if(res.status == 200){
                    if(res.data.length>0){
                        this.homestayData = res.data;
                        done();
                    }else{
                        this.homestayData = [];
                        done();
                    }
                }
            })
        },
        // 刷新
        refresh(done){
            this.query.page = 1;
            this.getData(this.query,done);
        },
        // 加载数据
        infinite(done){
            this.query.page++;
            this.$http.get('/room/get_list/',{params: this.query}).then(res=>{
                if(res.data.length>0){
                    this.homestayData = this.homestayData.concat(res.data);
                    done();
                }else{
                    done(true)
                }
            })
        }
    }
}



