import Picker from './picker/index'
export default {
    props:['defaultDuration'],
    name: 'DurationPicker',
    components: { Picker },
    data(){
        return{
            duration: this.defaultDuration,
            durationFilter: [1,1.5,2,2.5,3,3.5,4,4.5,5,5.5,6,6.5,7,7.5,8]
        }
    },
    methods:{
        cancel(){
            this.$emit('updateIsShow', false)
        },
        confirm(){
            this.$emit('getDuration', this.duration)
            this.$emit('updateIsShow', false);
        },
        getValue(val){
            this.duration = val;
        }
    }
}



