export default {
    data(){
        return{
            typeActiveIndex: 0,
            layoutActiveIndex: [],
            directionActiveIndex: [],
        }
    },
    methods:{
        // 筛选
        activeTab(array, index){
            if(array.indexOf(index)==-1){
                array.push(index);
            }else{
                array.splice(array.indexOf(index),1);
            }
        },
        // 传递数据
        submitData(){
            let filterData = {
                type: this.typeActiveIndex
            };
            this.directionActiveIndex.join('|')&&(filterData.face = this.directionActiveIndex.join('|'));
            this.layoutActiveIndex.join('|')&&(filterData.bed_count = this.layoutActiveIndex.join('|'));
            this.$emit('updateIsShow',false);
            this.$emit('getRentTypeFilter',filterData);
        },
        resetData(){
            this.typeActiveIndex= 0;
            this.layoutActiveIndex= [];
            this.directionActiveIndex= [];
            let filterData = {
                type: this.typeActiveIndex,
                face:null,
                bed_count:null
            };
            this.$emit('updateIsShow',false);
            this.$emit('getRentTypeFilter',filterData);
        }
    }
}



